/**
 * @author GuangHui
 * @description 图片类,应包含图片本身 Graph+图片的标题GraphTitle
 */

import { Base, IChild, IGroup, IPos, ISize, ISurround, LayoutItemTypeEnum, SurrounTypeEnum } from '../base'
import { Graph, GraphTitle } from './'

/** 限制下 Img 的child 类型 */
export type ImgChild = Graph | GraphTitle

export class Img extends Base implements IChild, ISurround {
  layoutItemType: LayoutItemTypeEnum = LayoutItemTypeEnum.IMG
  canLineBreak: boolean = false

  childs: [Graph, GraphTitle] | [Graph] | [] = []

  surroundType: SurrounTypeEnum = SurrounTypeEnum.NONE

  getPos(): IPos | Promise<IPos> {
    throw new Error('Method not implemented.')
  }

  getSize(): ISize | Promise<ISize> {
    throw new Error('Method not implemented.')
  }

  haveTitle() {
    return this.childs.length > 1
  }
}
